*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
#root{
    color: #c0e1f6;
    height: 100vh;
    background-color: #3b5ec2;
}
.heading{
    color: #c0e1f6;
    text-align: center;
    font-size: 2rem;
    background-color: #6a86d0;
    height: 12%;
    position: relative;
    top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.maindiv{
    margin: 27px auto;
    height: 73%;
}
.citydiv{
    height: 60%;
    display: flex;
    flex-flow: column wrap;
    justify-content: space-around;
    align-items: center;
}
#inputsearch{
    max-width: 189px;
    border: none;
    max-height: 30px;
    height: 100%;
    font-size: 1rem;
    padding: 10px;
    margin-right: 3px;
    background-color: #ffffffe3;
}
.searchh1{
    font-size: 2.1rem;
    text-transform: capitalize;
    color: #c0e1f6;
    border-top: 1px solid;
    height: 57px;
    line-height: 51px;
    border-bottom: 1px solid;
}
.tempdiv{
    display: flex;
    flex-flow: column wrap;
    justify-content: space-around;
    align-items: center;
    height: 40%;
    background-color: #6a86d0;
    margin-top: 22px;
}
.skydetailC{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.weatherimg{
    animation: rotation 3s infinite linear;
    position: relative;
}
@keyframes rotation {
    0% {
        left: 0;
      }
      50% {
        left: 2%;
      }
      100% {
        left: 0;    
      }
  }
  .imgkadiv{
    width: 51%;
    text-align: center;
  }
  .ppp{
    font-size: 1.1rem;
  }
  
  @media screen and (max-width: 500px) {
    .heading{
        font-size: 1.8rem;
        height: 8%;
    }
    .ppp {
        font-size: 14px;
    }
    .maindiv {
        height: 49%;
        border-radius: 133px;
    }      
    .citydiv {
        justify-content: space-evenly;
    }
  }
  